import React, { Component } from 'react';
import { Edit3, Trash2 } from 'react-feather';

class Drivers extends Component {
    constructor(props) {
        super(props);
        this.state = {}
    }
    render() {
        return (
            <div>
                {/* ============================================================== */}
                {/* Start Page Content here */}
                {/* ============================================================== */}
                <div className="content-page">
                    <div className="content">
                        {/* Start Content*/}
                        <div className="container-fluid">
                            <div className="row page-title">
                                <div className="col-md-6">
                                    <h4 className="mb-1 mt-0">Drivers</h4>
                                </div>
                                <div className="col-md-6 buttons text-right">
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-12">
                                    <div className="card">
                                        <div className="card-body">
                                            <table id="datatable-buttons" className="table table-striped dt-responsive nowrap">
                                                <thead>
                                                    <tr>
                                                        <th>Driver</th>
                                                        <th>CNIC</th>
                                                        <th>License #</th>
                                                        <th>Mobile #</th>
                                                        <th>Age</th>
                                                        <th>Gender</th>
                                                        <th>Email</th>
                                                        <th>Vehicle</th>
                                                        <th>Service Provider</th>
                                                        <th>Contact Number</th>
                                                        <th>Area Covered</th>
                                                        <th>Rating</th>
                                                        <th>Ride Status</th>
                                                        <th>Presence</th>
                                                        <th>&nbsp;</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr>
                                                        <td><img src="assets/images/user.jpg" /> Driver 1</td>
                                                        <td>1111-1111111-1</td>
                                                        <td>22444-9988-9</td>
                                                        <td>777777777</td>
                                                        <td>29</td>
                                                        <td>Male</td>
                                                        <td>sam@test.com</td>
                                                        <td>ALA-123</td>
                                                        <td>SP 1</td>
                                                        <td>0321 5555555</td>
                                                        <td>I-8 to I-9</td>
                                                        <td>4.5</td>
                                                        <td>Active</td>
                                                        <td>100%</td>
                                                        <td align="right">
                                                            <a href="#" className="edit-icon" data-toggle="modal" data-target="#add-modal">
                                                                <Edit3 />
                                                            </a>
                                                            <a href="#" className="delete-icon">
                                                                <Trash2 />
                                                            </a>
                                                        </td>
                                                    </tr>

                                                </tbody>
                                            </table>
                                        </div> {/* end card body*/}
                                    </div> {/* end card */}
                                </div>
                                {/* end col-12 */}
                            </div> {/* end row */}
                            {/* modals */}
                            {/* Add New Event MODAL */}
                            <div className="modal fade" id="add-modal" tabIndex={-1}>
                                <div className="modal-dialog">
                                    <div className="modal-content">
                                        <div className="modal-header py-3 px-4 border-bottom-0 d-block">
                                            <button type="button" className="close" data-dismiss="modal" aria-hidden="true"></button>
                                            <h5 className="modal-title" id="modal-title">Edit Driver Details</h5>
                                        </div>
                                        <div className="modal-body p-4">
                                            <form className="needs-validation" name="event-form" id="form-event" noValidate>
                                                <div className="row">
                                                    <div className="col-12">
                                                        <div className="form-group">
                                                            <label className="control-label">Driver</label>
                                                            <input className="form-control" placeholder="Driver" type="text" required />
                                                            <div className="invalid-feedback">Please provide a Driver</div>
                                                        </div>
                                                        <div className="form-group">
                                                            <label className="control-label">Vehicle</label>
                                                            <input className="form-control" placeholder="Vehicle" type="text" required />
                                                            <div className="invalid-feedback">Please provide a valid Vehicle</div>
                                                        </div>
                                                        <div className="form-group">
                                                            <label className="control-label">Service Provider</label>
                                                            <input className="form-control" placeholder="Service Provider" type="text" required />
                                                            <div className="invalid-feedback">Please provide a Service Provider</div>
                                                        </div>
                                                        <div className="form-group">
                                                            <label className="control-label">Contact Number</label>
                                                            <input className="form-control" placeholder="Contact Number" type="text" required />
                                                            <div className="invalid-feedback">Please provide a Contact Number</div>
                                                        </div>
                                                        <div className="form-group">
                                                            <label className="control-label">Area Covered</label>
                                                            <input className="form-control" placeholder="Area Covered" type="text" required />
                                                            <div className="invalid-feedback">Please provide a Area Covered</div>
                                                        </div>
                                                        <div className="form-group">
                                                            <label className="control-label">Rating</label>
                                                            <input className="form-control" placeholder="Rating" type="text" required />
                                                            <div className="invalid-feedback">Please provide a Rating</div>
                                                        </div>
                                                        <div className="form-group">
                                                            <label className="control-label">Ride Status</label>
                                                            <input className="form-control" placeholder="Ride Status" type="text" required />
                                                            <div className="invalid-feedback">Please provide a Ride Status</div>
                                                        </div>
                                                        <div className="form-group">
                                                            <label className="control-label">Attendance</label>
                                                            <input className="form-control" placeholder="Attendance" type="text" required />
                                                            <div className="invalid-feedback">Please provide a Attendance</div>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className="row mt-2">
                                                    <div className="col-12 text-right">
                                                        <button type="button" className="btn btn-light mr-1" data-dismiss="modal">Close</button>
                                                        <button type="submit" className="btn btn-success" id="btn-save-event">Update</button>
                                                    </div>
                                                </div>
                                            </form>
                                        </div>
                                    </div> {/* end modal-content*/}
                                </div> {/* end modal dialog*/}
                            </div>
                            {/* end modal*/}
                        </div> {/* container-fluid */}
                    </div> {/* content */}
                    {/* Footer Start */}
                    <footer className="footer">
                        <div className="container-fluid">
                            <div className="row">
                                <div className="col-12">
                                    2020  Pick&amp;Drop. All Rights Reserved.
              </div>
                            </div>
                        </div>
                    </footer>
                    {/* end Footer */}
                </div>
                {/* ============================================================== */}
                {/* End Page content */}
                {/* ============================================================== */}
            </div>
        );
    }
}

export default Drivers;